export default{
  data() {
    return {
      doctorName: this.$store.state.doctor.substr(0, 1),
      num: 200,
      desc: '',
      currentType: -1,
      active: false,
      illnessType: '',
      illnessList: [
        {
          title: '心脑血管'
        },
        {
          title: '心内科'
        },
        {
          title: '神经外科'
        },
        {
          title: '神经内科'
        },
        {
          title: '妇科'
        },
        {
          title: '便秘'
        },
        {
          title: '其他儿科常见问题'
        }
      ]
    }
  },
  watch: {
    desc: function(val) {
      this.num = 200 - val.length
    }
  },
  created() {},
  methods: {
    selectIllnessType(index, title) {
      this.illnessType = title
      this.currentType = index
      this.active = false
    },
    goNext() {
      this.$router.push({name: 'Search'})
    },
    selectIllness() {
      this.active = !this.active
    }
  }
}



